import React from 'react'
import Link from 'gatsby-link'
import Img from "gatsby-image"



const singleMeetup = ({node}) => {
    return (
        <div style={{
            marginBottom: '1.5rem',
            padding: '1.5rem',
            border: '1px solid #ccc'
        }}>

            <div>
                <div>{node.meetupDetails.childMarkdownRemark.html}</div>
            </div>
        </div>
    )
}


const oneMeetupPage = (props) => {

    console.log(props)
    return (
        <div>
            
        </div>
    )
}

export default oneMeetupPage




export const singlemeetuplistQuery = graphql`
    query singlemeetuplistQuery{    
  contentfulMeetup(
    id: {
      eq: "c2NMlVbwRCgeoQI0OWuGska" 
      }
  ) {   
  id  
  meetupDetails {
    childMarkdownRemark{
      html
    }
  }
    
    
}}
`


